* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#container {
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}

h1 {
  text-align: center;
  font-family: Playfair Display, "Serif";
}

#header {
  text-align: center;
  background-color: #f4f4f4;
  flex-direction: column;
  height: 200px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

#selectFormDiv {
  justify-content: space-between;
  display: flex;
}

#searchbarForm {
  background-color: #fff;
  border: 2px solid #ddd;
  border-radius: 25px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

#searchbar {
  color: #333;
  background-color: #0000;
  border: none;
  outline: none;
  flex: 1;
  padding: 10px 15px;
  font-size: 16px;
}

#searchbar::placeholder {
  color: #aaa;
}

#searchbarBtn {
  color: #fff;
  cursor: pointer;
  background-color: #95a0a8;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-size: 18px;
  transition: background-color .3s, transform .2s;
  display: flex;
}

#searchbarBtn:hover {
  background-color: #005bb5;
  transform: scale(1.1);
}

#selectFormOne {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 200px;
  height: 0;
  display: flex;
}

#category, #price {
  color: #333;
  margin-bottom: 5px;
  font-family: Playfair Display, serif;
  font-size: 18px;
}

#categoryFilter, #priceFilter {
  color: #333;
  background-color: #fff;
  border: 2px solid #ddd;
  border-radius: 25px;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-family: Playfair Display, serif;
  font-size: 16px;
  transition: all .3s;
  box-shadow: 0 4px 6px #0000001a;
}

#categoryFilter:hover, #priceFilter:hover, #categoryFilter:focus, #priceFilter:focus {
  border-color: #005bb5;
  outline: none;
  box-shadow: 0 0 5px #005bb599;
}

#categoryFilter option, #priceFilter option {
  color: #333;
  background-color: #fff;
  padding: 10px;
  font-family: Playfair Display, serif;
  font-size: 16px;
}

#selectFormTwo {
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 0;
  margin-left: 15rem;
  display: flex;
}

#priceHighLow, #ratingHighLow {
  color: #333;
  margin-bottom: 5px;
  font-family: Playfair Display, serif;
  font-size: 18px;
}

#priceHighLowSort, #ratingHighLowSort {
  color: #333;
  background-color: #fff;
  border: 2px solid #ddd;
  border-radius: 25px;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-family: Playfair Display, serif;
  font-size: 16px;
  transition: all .3s;
  box-shadow: 0 4px 6px #0000001a;
}

#priceHighLowSort:hover, #ratingHighLowSort:hover, #priceHighLowSort:focus, #ratingHighLowSort:focus {
  border-color: #005bb5;
  outline: none;
  box-shadow: 0 0 5px #005bb599;
}

#priceHighLowSort option, #ratingHighLowSort option {
  color: #333;
  background-color: #fff;
  padding: 10px;
  font-family: Playfair Display, serif;
  font-size: 16px;
}

h3 {
  text-align: center;
  height: 44px;
  margin: 20px 0;
  font-size: 20px;
}

#productContainer {
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.card {
  text-align: center;
  max-width: 250px;
  margin: auto;
  font-family: arial;
  box-shadow: 0 4px 8px #0003;
}

.card img {
  width: 250px;
  height: 250px;
}

.discountedPrice {
  color: gray;
  font-size: 22px;
}

.card button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #000;
  border: none;
  outline: 0;
  width: 100%;
  padding: 12px;
  font-size: 18px;
  transition: all .3s;
}

.card button:hover {
  opacity: .7;
  font-size: 1.2rem;
}

@media (width <= 1024px) {
  #searchbarForm {
    justify-content: center;
    margin-bottom: 10px;
    display: flex;
  }

  #selectFormDiv {
    justify-content: center;
    margin-top: 10px;
    display: flex;
  }

  #selectFormOne, #selectFormTwo {
    flex-direction: row;
    width: auto;
    height: auto;
    margin: 0;
    display: flex;
  }

  #category, #price, #priceHighLow, #ratingHighLow {
    margin-right: 10px;
  }

  #categoryFilter, #priceFilter, #priceHighLowSort, #ratingHighLowSort {
    width: auto;
    min-width: 150px;
    margin-bottom: 0;
  }
}

@media (width <= 980px) {
  #selectFormOne, #selectFormTwo {
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    height: auto;
    margin-bottom: 10px;
    display: flex;
  }

  #category, #price, #priceHighLow, #ratingHighLow {
    display: none;
  }

  h1 {
    font-size: 1.5rem;
  }
}

@media (width <= 540px) {
  #selectFormDiv {
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    display: flex;
  }

  #selectFormTwo, #searchbarForm {
    margin: 0;
  }

  #categoryFilter, #priceFilter, #priceHighLowSort, #ratingHighLowSort {
    width: 100px;
    max-width: 120px;
    padding: 5px;
    font-size: 14px;
  }
}
